<template>
	<layout>
		<div class="clearfix center p2">
			<p class="h1">Refeed Vespa Feeds</p>
			<div class="col col-12 border-box p1 center">
				<a class="btn btn-primary btn-big bg-gray col-12 max-width-1" v-if="pendding == 'collection'">1. Collections Refeeding...</a>
				<a class="btn btn-primary btn-big bg-teal col-12 max-width-1" v-else @click="refeed('collection')">Refeed Collections</a>
			</div>
			<div class="col col-12 border-box p1 center">
				<a class="btn btn-primary btn-big bg-gray col-12 max-width-1" v-if="pendding == 'product'">2. Products Refeeding...</a>
				<a class="btn btn-primary btn-big bg-orange col-12 max-width-1" v-else @click="refeed('product')">Refeed Products</a>
			</div>
			<div class="col col-12 border-box p1 center">
				<a class="btn btn-primary btn-big bg-gray col-12 max-width-1" v-if="pendding == 'category'">3. Categories Refeeding...</a>
				<a class="btn btn-primary btn-big bg-yellow col-12 max-width-1" v-else @click="refeed('category')">Refeed Categories</a>
			</div>
			<div class="col col-12 border-box p1 center gray">
				<p>è¯·æŒ‰ç…§ä»¥ä¸Š 1ã€2ã€3 çš„é¡ºåº Refeed.</p>
				<p>Refeed è¡¨ç¤ºä»Ž Shopify é‡æ–°èŽ·å–æ•°æ®ï¼Œå¹¶é‡æ–°å‘å¸ƒåˆ° Vespa Search Engine.</p>
				<p>å¦‚æžœä½ ä¸ç¡®å®šæˆ–ä¸çŸ¥é“ Refeed æ˜¯ä»€ä¹ˆæ„æ€ï¼Œè¯·è”ç³»ç®¡ç†å‘˜ã€‚ï¼ˆè§é¡µé¢åº•éƒ¨ï¼‰</p>
			</div>
		</div>
		<div class="fixed top-0 bottom-0 left-0 right-0 z1 flex items-center justify-center flex-wrap content-center center bg-yellow" v-show="!!pendding">
			<img src="../assets/loading-bubbles.svg" width="100" height="100">
			<h1 class="m0 col-12 mx-auto center">Refeeding...Please wait...</h1>
		</div>
	</layout>
</template>

<script>
import Layout from '@/components/Layout'
export default {
	name: 'VendorMap',
	data () {
		return {
			pendding: false
		}
	},
	methods: {
		refeed (t) {
			let self = this
			self.pendding = t
			self.$http.get('websh/refeed?t=' + t).then(({ data }) => {
				self.pendding = ''
				if (data == 0) swal('Success', '', 'success')
				else swal('Oops...', 'Please try again later', 'error')
			}, (response) => {
				self.pendding = ''
				swal('Oops...', 'Please try again later', 'error')
			})
		}
	},
	components: {
		Layout
	}
}
</script>

<style scoped></style>



